const liffId = CONFIG.NEXT_PUBLIC_LIFF_ID;
let isLineInited = false;

const initLine = async () => {
    await new Promise((resolve) => setTimeout(resolve, 3000));
    const liff = (await import('@line/liff')).default
    try {
        await liff.init({ liffId });
        isLineInited = true;
    } catch (error: unknown) {
        if (error instanceof Error) {
            console.error('liff init error', error.message);
        }
    }
}


const RegisterForm = async () => {
    const theme = useTheme();
    const u = theme.palette.background.default;
    await initLine();
    return (

        <Grid container spacing={1}
            alignItems="center"
            justifyContent="center"
            display="flex"
            height="100vh"
            sx={{ backgroundColor: u }}>
            <Grid>
                <Box sx={{ py: 5, bgcolor: "white", px: 5, borderRadius: 4 }} >
                    <Box sx={{ py: 1.5 }} alignItems="center" justifyContent="center" textAlign="center">
                        <Typography sx={{ fontSize: '1rem' }}>ลงทะเบียน</Typography>
                    </Box>

                    <TextField
                        id="standard-basic"
                        placeholder='เลขประจำตัวผู้เสียภาษี'
                        fullWidth
                        slotProps={
                            {
                                input: {
                                    endAdornment: <UserCheck size={32} />
                                }
                            }
                        }
                    />
                    <Box sx={{ py: 1.5 }}  >
                        <RegisterButton />
                    </Box>

                </Box>
            </Grid>
        </Grid>


    )
}

export default RegisterForm
